<template>
    <div class="left-side-container" ref="wrapper">
        <div class="home-headline-container">
            <img class="home-headline" src="img/home-headline@2x.png" alt="Design your own INK-credible Adventure">
            <StartButton id="start-btn" :startOnPressedHandler="startOnPressedHandler" />
        </div>
    </div>
</template>

<script>
    import { TweenMax, Linear } from 'gsap';
    import StartButton from "../UI/StartButton";
    export default {
        name: "HomeStep",
        components: {StartButton},
        props: ['onPressedHandler'],
        data () {
            return { pressed: false }
        },
        mounted() {
            window.addEventListener('resize', () => this.onResizeHandler());
            this.onResizeHandler();
        },
        methods: {
            onResizeHandler () {
                if ( window.innerWidth > window.innerHeight + 250  ) {
                    const contentHeight = 1100;
                    if (window.innerHeight < contentHeight) {
                        const scale = window.innerHeight / contentHeight;
                        this.$refs.wrapper.style.transform = `scale(${scale})`
                    } else {
                        this.$refs.wrapper.style.transform = `scale(1)`
                    }
                    this.$refs.wrapper.style.width = "40%";
                } else {
                    const contentWidth = 1400;
                    if (window.innerWidth < contentWidth) {
                        const scale = window.innerWidth / contentWidth;
                        this.$refs.wrapper.style.transform = `scale(${scale})`
                    } else {
                        this.$refs.wrapper.style.transform = `scale(1)`
                    }
                    this.$refs.wrapper.style.width = "50%";

                }
            },
            startOnPressedHandler () {
                console.log("HomeStep.startOnPressedHandler");
                if ( ! this.pressed ) {
                    this.onPressedHandler();
                    this.pressed = true;
                }
            },
            getOut () {
                TweenMax.set(this.$refs.wrapper, { y: -5000, alpha: 0 });
                this.pressed = true;
            },
            getIn () {
                TweenMax.set(this.$refs.wrapper, { x: 0, y: 0 });
                this.pressed = false;
            },
            animateIn () {
                this.getIn();
                TweenMax.to(this.$refs.wrapper, 0.3, { alpha: 1, ease: Linear.easeNone });
                // TweenMax.to(this.$refs.wrapper, 0.6, { x: -40, ease: Expo.easeOut });
            },
            animateOut () {
                // TODO: Kill mouse action
                TweenMax.to(this.$refs.wrapper, 0.3, { alpha: 0, ease: Linear.easeNone });
                TweenMax.delayedCall(0.3, () => this.getOut());
            }
        }
    }
</script>

<style scoped>
    .home-headline-container {
        flex: 1;
        flex-direction: column;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        /*border: 2px solid green;*/
    }
    img.home-headline {
        max-width: 560px!important;
        max-height: 259px!important;
    }
</style>