<template>
    <div class="torn-paper-container">
        <div class="torn-paper" ref="tornPaperEl">
<!--            <img src="img/textured-bg@2x.png" ref="actualImageEl" alt="">-->
        </div>
    </div>
</template>

<script>
    const VueConfig = require('../../../vue.config.js');
    export default {
        name: "TornPaper",
        data () {
            return { width: 0 };
        },
        mounted() {
            window.addEventListener('resize', () => this.onResizeHandler());
            this.$refs.tornPaperEl.style.backgroundImage = `url(${ VueConfig.publicPath }img/textured-bg@2x.png)`;


            this.onResizeHandler();
        },
        methods: {
            onResizeHandler () {
                // if (window.innerWidth > window.innerHeight) {

                // Resize based on width
                const finalImageWidth = 0.8 * window.innerWidth;
                this.$refs.tornPaperEl.style.width = `${finalImageWidth}px`;
                this.$refs.tornPaperEl.style.height = `100%`;
                this.$refs.tornPaperEl.style.marginLeft = `0px`;

                const currentWidthOfImage = this.$refs.tornPaperEl.offsetWidth;
                if ( currentWidthOfImage === 0 ) {
                    setTimeout(() => this.onResizeHandler(), 100);
                }

                const multiplier = (window.innerWidth > window.innerHeight) ? 0.8 : 0.6;
                const acceptableMaxWidth = multiplier * window.innerWidth;
                // Does the current width occupy more than 70% of the screen?
                if ( currentWidthOfImage > acceptableMaxWidth ) {
                    // if yes, move it left so it covers only up to 70% of screen
                    this.$refs.tornPaperEl.style.backgroundPositionX = `-${ currentWidthOfImage - acceptableMaxWidth }px`
                }

                    //
                    if (this.width !== this.$refs.tornPaperEl.offsetWidth) {
                        this.width = this.$refs.tornPaperEl.offsetWidth;
                        this.$emit('texture-bg-width', this.width / 1.7);
                    }
                // }

            }
        }
    }
</script>

<style scoped>
    .torn-paper-container {
        /*border: 2px solid red;*/
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        pointer-events: none;
    }
    .torn-paper {
        margin-left: 0;
        /*height: 100%!important;*/
        background-size: cover;
        background-repeat: no-repeat;
    }

