import {Expo} from "gsap";import {TimelineMax} from "gsap";
<template>
    <div class="left-side-container" ref="wrapper">
        <div class="step-container" ref="animateEl">
            <img :src="instructionsSrc" alt="" class="image">
            <div class="button-group">
                <Button color="grey" label="Back" :onPressed="onBack" />
                <Button color="red" label="Next" :onPressed="onNext" :class="{ 'disabled': !canNext, 'clickable': canNext }" />
            </div>
        </div>
    </div>
</template>

<script>
    import { TimelineMax, TweenMax, Linear } from 'gsap';
    import Button from "../UI/Button";

    export default {
        name: "Step",
        components: {
            Button
        },
        props: ['step', 'nextPressedHandler', 'backPressedHandler', 'canNext'],
        data () {
            return {
                show: false, instructionsSrc: '',
                inTween: null,
            }
        },
        mounted () {

            window.addEventListener('resize', () => this.onResizeHandler());
            this.onResizeHandler();

            this.instructionsSrc = `img/step${ this.step }-headline@2x.png`;
            this.getOut();

            const tl = new TimelineMax({ paused: true });
            tl.add(TweenMax.to(this.$refs.wrapper, 0.15, { autoAlpha: 1.0, ease: Linear.easeNone }));
            this.inTween = tl
        },
        methods: {
            onResizeHandler () {
                if ( window.innerWidth >= window.innerHeight ) {
                    const contentHeight = 1300;
                    if (window.innerHeight < contentHeight) {
                        const scale = window.innerHeight / contentHeight;
                        this.$refs.wrapper.style.transform = `scale(${scale})`
                    } else {
                        this.$refs.wrapper.style.transform = `scale(1)`
                    }
                    this.$refs.wrapper.style.width = "35%";
                } else {
                    const contentWidth = 1500;
                    if (window.innerWidth < contentWidth) {
                        const scale = window.innerWidth / contentWidth;
                        this.$refs.wrapper.style.transform = `scale(${scale})`
                    } else {
                        this.$refs.wrapper.style.transform = `scale(1)`
                    }
                    this.$refs.wrapper.style.width = "50%";
                }
            },
            getOut () {
                TweenMax.set(this.$refs.wrapper, { left: "-5000px", alpha: 0 });
            },
            getIn () {
                TweenMax.set(this.$refs.wrapper, { left: "0px" });
            },
            animateIn () {
                this.getIn();
                //
                // const duration = Number( ((Math.random() * 0.3) + .8).toFixed(2) );
                // // const duration = (Math.random() * 0.3) + .8;
                // console.log("duration", duration);
                // const t = TweenMax.fromTo(this.$refs.centerEl, duration, {x: this.sx, y: this.sy}, {
                //     force3D: true,
                //     overwrite: true,
                //     delay: Math.random() * 0.3,
                //     x: this.fx,
                //     y: this.fy,
                //     ease: Expo.easeOut,
                //     lazy: true,
                //     immediateRender: true,
                //     callbackScope: [],
                //     useFrames: true,
                //     // onComplete: () => this.startSwim()
                // });
                // const tl = new TimelineMax();
                // tl.add(t);
                // tl.tweenTo(duration - 0.01);
                //
                // const t = TweenMax.to(this.$refs.wrapper, 0.15, { paused: true, alpha: 1.0, ease: Linear.easeNone });
                // t.play();

                TweenMax.to(this.inTween, { progress: 1.0 });
                // this.inTween.tweenTo(this.inTween.totalDuration());
            },
            animateOut () {
                // TODO: Kill mouse action
                // this.inTween.tweenTo(0);
                TweenMax.to(this.inTween, { progress: 0 });
                // TweenMax.to(this.$refs.wrapper, 0.15, { alpha: 0, ease: Linear.easeNone });
                TweenMax.delayedCall(0.16, () => this.getOut());
            },
            onBack () {
                if ( this.backPressedHandler ) {
                    this.backPressedHandler();
                }
            },
            onNext () {
                if ( this.nextPressedHandler ) {
                    this.nextPressedHandler();
                }
            }
        }
    }
</script>

<style scoped>

    .step-container {
        flex: 1;
        flex-direction: column;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        /*border: 2px solid green;*/
    }

    img.image {
        transform: scale(0.5) translateX(50%);
        margin-bottom: -100px;
        /*max-width: 435px!important;*/
        /*max-height: 247px!important;*/
    }
</style>