<template>
    <div ref="transitionEl" class="transition">
        <LoaderAnimation/>
    </div>
</template>

<script>
    import { Expo, TweenMax} from 'gsap';
    import LoaderAnimation from "../UI/LoaderAnimation";

    export default {
        name: "Transition",
        components: {
            LoaderAnimation
        },
        mounted() {
            TweenMax.set(this.$refs.transitionEl, {width: window.innerWidth, height: window.innerHeight, x: -5000})
        },
        methods: {
            transitIn() {
                TweenMax.fromTo(this.$refs.transitionEl, 0.8,
                    {
                        width: '100vw',
                        height: '100vh',
                        x: 0,
                        y: '-100%'
                    },
                    {
                        width: '100vw',
                        height: '100vh',
                        x: 0,
                        y: '0%',
                        overwrite: true,
                        ease: Expo.easeOut,
                        onComplete: () => {
                            this.$emit('transit-in-complete')
                        }
                    });
            },
            transitOut() {
                // TweenMax.set(this.$refs.transitionEl, { top: 0, height: 1, x: 0 });
                TweenMax.fromTo(this.$refs.transitionEl, .8, {
                        width: '100vw',
                        height: '100vh',
                        x: 0,
                        y: '0%',
                    },
                    {
                        width: '100vw',
                        height: '100vh',
                        x: 0,
                        y: '-100%',
                        overwrite: true,
                        ease: Expo.easeOut,
                        onComplete: () => {
                            this.$emit('transit-out-complete')
                        }
                    });
            }
        }
    }
</script>

<style scoped>
    .transition {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-color: #FFFFFF;
        top: 0;
        left: 0;
    }
</style>