<template>
  <div id="app">
    <TextWatermark ref="textWatermarkEl"/>
    <PreloadedAssets ref="preloadedAssets"/>
    <NgsHomepageButton />
    <HomeStep ref="homeStep" :onPressedHandler="startToStep1" :onPressedHandler2="startCanvas"/>

    <Step :step="1" ref="step1Screen" :nextPressedHandler="step1ToStep2" :backPressedHandler="step1ToStart" :canNext="selectedBrushNum"/>
    <Step1Content :left="stepContentLeft" v-on:selected="selectBrush" ref="step1Content" />

    <Step :step="2" ref="step2Screen" :nextPressedHandler="step2ToStep3" :backPressedHandler="step2ToStep1" :canNext="selectedColorScheme"/>
    <Step2Content :left="stepContentLeft" v-on:selected="selectColorScheme" ref="step2Content" />

    <Step :step="3" ref="step3Screen" :nextPressedHandler="startCanvas" :backPressedHandler="step3ToStep2" :canNext="selectedCanvas"/>
    <Step3Content :left="stepContentLeft" v-on:selected="selectCanvas" ref="step3Content" />

    <TornPaper @texture-bg-width="updateStepContentLeft" ref="tornPaperEl"/>
    <Art ref="art" :selectedColorScheme="selectedColorScheme"
         :selectedBrushes="selectedBrushNum"
         :selectedCanvas="selectedCanvas"
         :removeGlobalElements="removeGlobalElements"
    />
    <BlurBackground ref="blurBackgroundEl"/>
    <VideoBg ref="videoBackgroundEl"/>
    <NGSLogo/>

    <Transition v-on:transit-in-complete="transitInComplete" v-on:transit-out-complete="transitOutComplete" ref="transition"/>
  </div>
</template>

<script>
  import {TweenMax} from 'gsap';
  import Step from "./components/LeftSide/Step";
  import Step1Content from "./components/RightSide/Step1Content";
  import Step2Content from "./components/RightSide/Step2Content";
  import Step3Content from "./components/RightSide/Step3Content";
  import NgsHomepageButton from "./components/UI/NgsHomepageButton";
  import TornPaper from "./components/LeftSide/TornPaper";
  import HomeStep from "./components/LeftSide/HomeStep";
  import VideoBg from "./components/UI/VideoBg";
  import NGSLogo from "./components/UI/NGSLogo";
  import Transition from "./components/Art/Transition";
  import Art from "./components/Art/Art";
  import BlurBackground from "./components/UI/BlurBackground";
  import PreloadedAssets from "./components/Utils/PreloadedAssets";
  import TextWatermark from "./components/UI/TextWatermark";

  export default {
    name: 'App',
    components: {
      HomeStep, NgsHomepageButton, TornPaper,
      VideoBg, NGSLogo, Step,
      Step1Content, Step2Content, Step3Content,
      Art, Transition, BlurBackground,
      PreloadedAssets, TextWatermark
    },
    data () {
      return {
        selectedBrushNum: null,
        selectedColorScheme: null,
        selectedCanvas: null,
        stepContentLeft: '50%',
      }
    },
    mounted () {
      this.transitInComplete = this.transitInComplete.bind(this);
      this.$refs.art.$el.style.left = '-10000px';
    },
    methods: {
      updateStepContentLeft (amt) {
        this.stepContentLeft = `${amt}px`;
      },
      // Selections
      selectBrush (num) {
        this.selectedBrushNum = num;
        this.$refs
      },
      selectColorScheme (num) {
        this.selectedColorScheme = num;
      },
      selectCanvas (num) {
        this.selectedCanvas = num;
        this.$refs.preloadedAssets.addImage( this.$refs.art.getCanvasUrl(num) )
      },

      // Transitions
      startToStep1 () {
        this.$refs.textWatermarkEl.hide();
        TweenMax.delayedCall(0.1, () => {
          this.$refs.homeStep.animateOut();
          this.$refs.blurBackgroundEl.animateIn();
        });
        TweenMax.delayedCall(0.3, () => {
          this.$refs.step1Screen.animateIn();
          this.$refs.step1Content.animateInFromRight();
        });

      },
      step1ToStart () {
        this.$refs.step1Screen.animateOut();
        this.$refs.step1Content.animateOutToRight();
        this.$refs.blurBackgroundEl.animateOut();
        TweenMax.delayedCall(0.2, () => {
          this.$refs.homeStep.animateIn();
          this.$refs.textWatermarkEl.positionForHome();
          this.$refs.textWatermarkEl.show();
        });
      },
      step1ToStep2 () {
        this.$refs.step1Screen.animateOut();
        this.$refs.step1Content.animateOutToLeft();
        TweenMax.delayedCall(0.2, () => {
          this.$refs.step2Screen.animateIn();
          this.$refs.step2Content.animateInFromRight();
        });
      },
      step2ToStep1 () {
        this.$refs.step2Screen.animateOut();
        this.$refs.step2Content.animateOutToRight();
        TweenMax.delayedCall(0.2, () => {
          this.$refs.step1Screen.animateIn();
          this.$refs.step1Content.animateInFromLeft();
        });
      },
      step2ToStep3 () {
        this.$refs.step2Screen.animateOut();
        this.$refs.step2Content.animateOutToLeft();
        TweenMax.delayedCall(0.2, () => {
          this.$refs.step3Screen.animateIn();
          this.$refs.step3Content.animateInFromRight();
        });
      },
      step3ToStep2 () {
        this.$refs.step3Screen.animateOut();
        this.$refs.step3Content.animateOutToRight();
        TweenMax.delayedCall(0.2, () => {
          this.$refs.step2Screen.animateIn();
          this.$refs.step2Content.animateInFromLeft();
        });
      },
      startCanvas () {
        this.$refs.step3Content.animateOutToLeft();
        this.$refs.step3Screen.animateOut();
        TweenMax.delayedCall(0.4, () => {
          this.$refs.transition.transitIn();
        });
      },
      transitInComplete () {
        this.$refs.art.$el.style.left = '0px';
        this.$refs.tornPaperEl.$el.style.left = '-5000px';
        this.$refs.videoBackgroundEl.stop();
        this.$refs.art.init();
        TweenMax.delayedCall(1.0, () => {
          this.$refs.transition.transitOut();
        });
        TweenMax.delayedCall(1.5,() => {
          this.$refs.textWatermarkEl.positionForArt();
          this.$refs.textWatermarkEl.show();
        });
      },
      transitOutComplete () {
        this.$refs.art.showInstructions();
      },
      removeGlobalElements () {
        this.$refs.textWatermarkEl.hide();
      },
    }
  }
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;

  margin: 0;
  padding: 0;
  /*border: 2px solid red;*/
  width: 100vw;
  height: 100vh;
}
</style>
