<template>
    <div class="step-content-container" ref="wrapper" :style="'left:' + this.left">
        <div class="step-content">
            <Ball ref="ball1" color="#333333" radius="50" y="-200" x="200"/>
            <Ball ref="ball2" color="#666" radius="200" y="-200" x="500"/>
            <Ball ref="ball3" color="#333333" radius="100" y="200" x="140"/>
            <Ball ref="ball4" color="#666" radius="30" y="100" x="-50"/>
            <Ball ref="ball5" color="#666" radius="30" y="150" x="5"/>
            <Ball ref="ball6" color="#999" radius="170" y="230" x="600"/>
            <Ball ref="ball7" color="#666" radius="20" y="320" x="270"/>
            <Ball ref="ball8" color="#333" radius="50" y="0" x="0"/>
            <Ball ref="ball9" color="#666" radius="20" y="-20" x="50"/>
            <Ball ref="ball10" color="#666" radius="20" y="-300" x="400"/>
            <Ball ref="ball11" color="#999" radius="14" y="-230" x="200"/>
            <ImageBall id="1" v-on:ball-selected="onBallSelected" onPressed="onBallSelected" ref="selection1" x="-190" y="-550"
                               src="img/step2-color-1@2x.png"
                               selectedSrc="img/step2-color-1-selected@2x.png"/>
            <ImageBall id="2" v-on:ball-selected="onBallSelected"  onPressed="onBallSelected" ref="selection2" x="50" y="-600"
                               src="img/step2-color-2@2x.png"
                               selectedSrc="img/step2-color-2-selected@2x.png" />
            <ImageBall id="3" v-on:ball-selected="onBallSelected" onPressed="onBallSelected" ref="selection3" x="-300" y="-200"
                               src="img/step2-color-3@2x.png"
                               selectedSrc="img/step2-color-3-selected@2x.png" />
            <ImageBall id="4" v-on:ball-selected="onBallSelected" onPressed="onBallSelected" ref="selection4" x="170" y="-330"
                               src="img/step2-color-4@2x.png"
                               selectedSrc="img/step2-color-4-selected@2x.png" />
            <ImageBall id="5" v-on:ball-selected="onBallSelected" onPressed="onBallSelected" ref="selection5" x="-60" y="-300"
                               src="img/step2-color-5@2x.png"
                               selectedSrc="img/step2-color-5-selected@2x.png" />
        </div>
    </div>
</template>

<script>
    import ImageBall from "./ImageBall";
    import Ball from "./Ball";
    import StepContentBase from "./StepContentBase";
    export default {
        name: "Step2Content",
        extends: StepContentBase,
        components: {
            Ball,
            ImageBall,
        },
        methods: {
            onBallSelected (obj) {
                if (this.$refs.selection1 !== obj) this.$refs.selection1.deselect();
                if (this.$refs.selection2 !== obj) this.$refs.selection2.deselect();
                if (this.$refs.selection3 !== obj) this.$refs.selection3.deselect();
                if (this.$refs.selection4 !== obj) this.$refs.selection4.deselect();
                if (this.$refs.selection5 !== obj) this.$refs.selection5.deselect();


                if (this.$refs.selection1 === obj) {
                    this.$emit('selected', 1);
                } else if (this.$refs.selection2 === obj) {
                    this.$emit('selected', 2);
                } else if (this.$refs.selection3 === obj) {
                    this.$emit('selected', 3);
                } else if (this.$refs.selection4 === obj) {
                    this.$emit('selected', 4);
                } else if (this.$refs.selection5 === obj) {
                    this.$emit('selected', 5);
                }
            }
        }
    }
</script>