<template>
    <div class="step-content-container" ref="wrapper" :style="'left:' + this.left">
        <div class="step-content">
            <Ball ref="ball1" color="#dca772" radius="240" y="-300" x="500"/>
            <Ball ref="ball2" color="#5a4165" radius="150" y="350" x="600"/>
            <Ball ref="ball3" color="#c09163" radius="100" y="200" x="140"/>
            <Ball ref="ball4" color="#2b63e3" radius="30" y="100" x="-50"/>
            <Ball ref="ball5" color="#ce6479" radius="30" y="150" x="5"/>
            <Ball ref="ball6" color="#000000" radius="10" y="300" x="230"/>
            <Ball ref="ball7" color="#8298c4" radius="20" y="320" x="270"/>
            <Ball ref="ball8" color="#78bca5" radius="50" y="0" x="0"/>
            <Ball ref="ball9" color="#6cb05b" radius="20" y="-20" x="50"/>
            <Ball ref="ball10" color="#d16c4e" radius="20" y="-20" x="200"/>
            <Ball ref="ball11" color="#000000" radius="14" y="-230" x="200"/>
            <ImageBall v-on:ball-selected="onBallSelected" onPressed="onBallSelected" ref="selection1" x="-150" y="-550"
                               src="img/step3-canvas-1@2x.png"
                               selectedSrc="img/step3-canvas-1-selected@2x.png"/>
            <ImageBall v-on:ball-selected="onBallSelected"  onPressed="onBallSelected" ref="selection2" x="100" y="-120"
                               src="img/step3-canvas-2@2x.png"
                               selectedSrc="img/step3-canvas-2-selected@2x.png" />
            <ImageBall v-on:ball-selected="onBallSelected" onPressed="onBallSelected" ref="selection3" x="-200" y="-200"
                               src="img/step3-canvas-3@2x.png"
                               selectedSrc="img/step3-canvas-3-selected@2x.png" />
            <ImageBall v-on:ball-selected="onBallSelected" onPressed="onBallSelected" ref="selection4" x="150" y="-440"
                               src="img/step3-canvas-4@2x.png"
                               selectedSrc="img/step3-canvas-4-selected@2x.png" />
        </div>
    </div>
</template>

<script>
    import ImageBall from "./ImageBall";
    import Ball from "./Ball";
    import StepContentBase from "./StepContentBase";
    export default {
        name: "Step2Content",
        extends: StepContentBase,
        components: {
            Ball,
            ImageBall,
        },
        methods: {
            onBallSelected (obj) {
                if (this.$refs.selection1 !== obj) this.$refs.selection1.deselect();
                if (this.$refs.selection2 !== obj) this.$refs.selection2.deselect();
                if (this.$refs.selection3 !== obj) this.$refs.selection3.deselect();
                if (this.$refs.selection4 !== obj) this.$refs.selection4.deselect();

                if (this.$refs.selection1 === obj) {
                    this.$emit('selected', 1);
                } else if (this.$refs.selection2 === obj) {
                    this.$emit('selected', 2);
                } else if (this.$refs.selection3 === obj) {
                    this.$emit('selected', 3);
                } else if (this.$refs.selection4 === obj) {
                    this.$emit('selected', 4);
                }
            }
        }
    }
</script>