<template>
    <div class="overallContainer">
        <div class="canvasContainer"
             :style="'margin-left:' + this.marginLeft + '; margin-top:' + this.marginTop"
             ref="canvasContainer">

        </div>
    </div>
</template>

<script>
    if (!('createImageBitmap' in window)) {
        console.log("polyfill");
        window.createImageBitmap = async function (data) {
            return new Promise((resolve, reject) => {
                let dataURL;
                if (data instanceof Blob) {
                    dataURL = URL.createObjectURL(data);
                } else if (data instanceof ImageData) {
                    const canvas = document.createElement('canvas');
                    const ctx = canvas.getContext('2d');
                    canvas.width = data.width;
                    canvas.height = data.height;
                    ctx.putImageData(data, 0, 0);
                    dataURL = canvas.toDataURL();
                } else {
                    throw new Error('createImageBitmap does not handle the provided image source type');
                }
                const img = document.createElement('img');
                img.addEventListener('load', function () {
                    resolve(this);
                });
                img.addEventListener('error', function () {
                    reject();
                });
                img.src = dataURL;
            });
        };
    }


    import * as THREE from 'three';

    export default {
        name: "art_content",
        props: ['selectedColors', 'selectedPaintType', 'totalAssets', 'selectedBackground', 'removeUI'],
        data: function () {
            return {
                materials: [],
                paintToRender: [],
                width: 0,
                height: 0,
                marginLeft: '0px',
                marginTop: '0px',
                //
                scene: null,
                sceneOrtho: null,
                camera: null,
                cameraOrtho: null,
                renderer: null,
                requestAnimationFrameId: null,
                takeScreenshot: false,
                screenshotData: null,
            }
        },
        mounted: function () {
            this.width = (screen.width >= 2560) ? 2560 : screen.width;
            this.height = (screen.height >= 1440) ? 1440 : screen.height;
        },
        beforeDestroy() {
            console.log("destroyed()");
            let k = this.paintToRender.length;
            while (k--) {
                const plane = this.paintToRender[k];
                plane.material.map.dispose();
                plane.geometry.dispose();
                plane.material.dispose();
                this.scene.remove(plane);
                this.paintToRender.splice(k, 1);
            }
            this.scene.dispose();
            this.sceneOrtho.dispose();
            for (let i = 0; i < this.materials.length; i++) {
                const m = this.materials[i];
                for (let j = 0; j < m.length; j++) {
                    this.materials[i][j].map.dispose();
                }
            }

            this.kill();
        },
        methods: {

            kill: function () {
                this.killEventListeners();
                if (this.requestAnimationFrameId) {
                    cancelAnimationFrame(this.requestAnimationFrameId);
                }
            },
            init: function () {

                console.log("ArtActual.init");
                this.loadAssets();
                this.buildCanvas();
            },
            start: function () {
                this.activateEventListeners();
            },
            loadAssets: function () {
                /*
                1. Load Brushes
                - Takes all the bitmap files
                - Generates materials based on the colors and bitmap files available
                */
                this.materials = [];

                // TODO: remove duplicate colors, but need to make sure that later on, selecting of colorIdx don't yield null
                const colors = this.selectedColors;
                const type = this.selectedPaintType;
                const totalAssets = Number(this.totalAssets);
                console.log(type, totalAssets);

                for (let i = 1; i <= totalAssets; i++) {
                    const filename = `art/paint/${type}${String(i)}.png`;
                    // console.log(filename);
                    new THREE.ImageBitmapLoader()
                        .setOptions({imageOrientation: 'none'})
                        .load(filename, (imageBitmap) => {

                            let tempMaterials = [];
                            let texture = new THREE.CanvasTexture(imageBitmap);
                            texture.minFilter = THREE.LinearFilter;
                            for (let i = 0; i < colors.length; i++) {
                                tempMaterials.push(new THREE.MeshBasicMaterial({
                                    map: texture,
                                    alphaMap: texture,
                                    transparent: true,
                                    alphaTest: 0.1,
                                    color: colors[i]
                                }));
                            }
                            this.materials.push(tempMaterials);
                        });
                }
            },
            buildCanvas: async function () {
                const {width, height} = this;

                this.scene = new THREE.Scene();
                this.sceneOrtho = new THREE.Scene();


                const logoLoader = new THREE.TextureLoader();
                logoLoader.load("art/watermarks/sy.png", logoTexture => {
                    const edgePadding = 0;
                    const sizeDivider = (screen.width > 1000) ? 1 : 2;
                    var logoMaterial = new THREE.SpriteMaterial({map: logoTexture});
                    logoMaterial.map.minFilter = THREE.LinearFilter;
                    var logoWidth = logoTexture.image.width;
                    var logoHeight = logoTexture.image.height;
                    var spriteTL = new THREE.Sprite(logoMaterial);
                    spriteTL.center.set(0, 0);
                    spriteTL.position.set(-this.width / 2 + edgePadding, -this.height / 2 + edgePadding, 1);
                    spriteTL.scale.set(logoWidth / sizeDivider, logoHeight / sizeDivider, 1);
                    this.sceneOrtho.add(spriteTL);
                });

                const logoLoader2 = new THREE.TextureLoader();
                logoLoader2.load("art/watermarks/ngs.png", logoTexture => {
                    const edgePadding = 0;
                    const sizeDivider = (screen.width > 1000) ? 1 : 2;
                    var logoMaterial = new THREE.SpriteMaterial({map: logoTexture});
                    logoMaterial.map.minFilter = THREE.LinearFilter;
                    var logoWidth = logoTexture.image.width;
                    var logoHeight = logoTexture.image.height;
                    var spriteTL = new THREE.Sprite(logoMaterial);
                    spriteTL.center.set(1.0, 1);
                    spriteTL.position.set(this.width / 2 - edgePadding, this.height / 2, 1);
                    spriteTL.scale.set(logoWidth / sizeDivider, logoHeight / sizeDivider, 1);
                    this.sceneOrtho.add(spriteTL);
                });

                const logoLoader3 = new THREE.TextureLoader();
                logoLoader3.load("art/watermarks/text.png", logoTexture => {
                    const edgePadding = 10;
                    const sizeDivider = (screen.width > 1000) ? 2 : 3;
                    var logoMaterial = new THREE.SpriteMaterial({map: logoTexture});
                    logoMaterial.map.minFilter = THREE.LinearFilter;
                    var logoWidth = logoTexture.image.width;
                    var logoHeight = logoTexture.image.height;
                    var spriteTL = new THREE.Sprite(logoMaterial);
                    spriteTL.center.set(1.0, 0);
                    spriteTL.position.set(this.width / 2 - edgePadding, -this.height / 2 + edgePadding, 1);
                    spriteTL.scale.set(logoWidth / sizeDivider, logoHeight / sizeDivider, 1);
                    this.sceneOrtho.add(spriteTL);
                });

                const bgLoader = new THREE.TextureLoader();
                bgLoader.load(this.selectedBackground, (tex) => {
                    // tex.minFilter = THREE.LinearFilter;
                    // this.scene.background = tex;
                    var bgWidth = tex.image.width;
                    var bgHeight = tex.image.height;

                    var spriteMaterial = new THREE.SpriteMaterial({map: tex});
                    spriteMaterial.map.minFilter = THREE.LinearFilter;

                    var sprite = new THREE.Sprite(spriteMaterial);
                    sprite.center.set(0.5, 0.5);
                    sprite.scale.set(bgWidth, bgHeight);
                    sprite.position.z = (screen.width > 1000) ? -1700 : -2400;
                    this.scene.add(sprite);

                });


                this.cameraOrtho = new THREE.OrthographicCamera(-this.width / 2, this.width / 2, this.height / 2, -this.height / 2, 1, 10);
                this.cameraOrtho.position.z = 5;

                this.camera = new THREE.PerspectiveCamera(60, width / height, 0.1, 2500);
                this.renderer = new THREE.WebGLRenderer({
                    antialias: false, alpha: true,
                    preserveDrawingBuffer: true
                });
                // this.renderer.setClearColor(0xFFFFFF, 0);
                this.renderer.setPixelRatio(window.devicePixelRatio ? window.devicePixelRatio : 1);
                this.renderer.setSize(width, height);
                this.renderer.autoClear = false;
                //

                // Remove all children
                this.$refs.canvasContainer.innerHTML = "";
                this.$refs.canvasContainer.appendChild(this.renderer.domElement);
                this.camera.position.z = 5;

                //
                this.requestAnimationFrameId = requestAnimationFrame(() => this.render());
            },
            render () {

                this.marginLeft = `0px`;//`-${Math.round((this.width - window.innerWidth) / 2.0)}px`;
                this.marginTop = `0px`;//`-${Math.round((this.height - window.innerHeight) / 2.0)}px`;

                let i = this.paintToRender.length;
                while (i--) {
                    const plane = this.paintToRender[i];
                    if (plane.material.opacity <= 0) {
                        plane.material.map.dispose();
                        plane.geometry.dispose();
                        plane.material.dispose();
                        this.scene.remove(plane);
                        this.paintToRender.splice(i, 1);
                    }
                }
                for (let i = 0; i < this.paintToRender.length; i++) {
                    const plane = this.paintToRender[i];
                    plane.position.z -= 0.00012;
                    plane.material.opacity -= 0.0001;
                    plane.rotation.z = Math.sin(plane.seed);
                    plane.seed += 0.0001;
                }
                //
                this.renderer.clear();
                this.renderer.render(this.scene, this.camera);
                if (this.takeScreenshot) {
                    this.renderer.clearDepth();
                    this.renderer.render(this.sceneOrtho, this.cameraOrtho);
                    this.actualScreenshot();
                } else {
                    this.requestAnimationFrameId = requestAnimationFrame(() => this.render());
                }

                this.resizeCanvasToContainInScreen();

            },

            resizeCanvasToContainInScreen () {
                const domElement = this.renderer.domElement;
                domElement.style.width = "100%";
                domElement.style.height = "auto";
            },

            activateEventListeners: function () {
                this.$refs.canvasContainer.addEventListener('mousedown', this.layPaint, true);
                this.$refs.canvasContainer.addEventListener('touchstart', this.layPaint, true);

                if ( typeof( DeviceMotionEvent ) !== "undefined" && typeof( DeviceMotionEvent.requestPermission ) === "function" ) {
                    DeviceMotionEvent.requestPermission()
                        .then(permissionState => {
                            if (permissionState === 'granted') {
                                window.addEventListener('devicemotion', (e) => {
                                    const { accelerationIncludingGravity} = e;
                                    const accel = (accelerationIncludingGravity.x + accelerationIncludingGravity.y + accelerationIncludingGravity.z) / 3.0;
                                    if ( this.prevAcceleration !== -1 ) {
                                        console.log(Math.abs(this.prevAcceleration - accel));
                                        if (Math.abs(this.prevAcceleration - accel) > 1.2) {
                                            this.layPaint();
                                            console.log(e)
                                        }
                                    }
                                    this.prevAcceleration = accel;
                                });
                            } else {
                                alert("Permissions for Motion Sensor not available. Tap on the screen to start making art!");
                            }
                        })
                        .catch(console.error);
                } else {
                    window.addEventListener('devicemotion', (e) => {
                        const {acceleration} = e;
                        if ((acceleration.x + acceleration.y + acceleration.z) / 3 > 2.0) {
                            this.layPaint();
                            console.log(e)
                        }
                    });
                }
            },
            killEventListeners: function () {
                this.$refs.canvasContainer.removeEventListener('click', this.layPaint, true);
                this.$refs.canvasContainer.removeEventListener('touchstart', this.layPaint, true);
            },
            layPaint: function () {
                const distanceFromCamera = Math.random() * 4 + 1;//(Math.random() >= nearProbability) ? Math.random() * 4 + 4 : Math.random() * 4;

                const pos = new THREE.Vector2();
                const extraWidth = this.width * 0.5;
                console.log('distanceFromCamera', distanceFromCamera);
                const extraHeight = this.height * 0.8;
                pos.x = Math.random() * extraWidth - (extraWidth * 0.5);
                pos.y = Math.random() * extraHeight - (extraHeight * 0.5);
                pos.z = 0;
                const raycaster = new THREE.Raycaster();
                raycaster.setFromCamera(pos, this.camera);

                const realPos = raycaster.ray.at(distanceFromCamera, pos);


                const matIdx = Math.floor(Math.random() * this.materials.length);
                const colorIdx = Math.floor(Math.random() * this.selectedColors.length);
                let size = Math.round(Math.random() * 3 + 3);
                if (this.selectedPaintType === "Dry") {
                    size = Math.round(Math.random() * 3 + 2);
                } else if (this.selectedPaintType === "Wet") {
                    size = Math.round(Math.random() * 3 + 2);
                }
                // const geometry = new THREE.PlaneGeometry(size, size, 0.1);
                const geometry = new THREE.PlaneBufferGeometry(size, size, 0.1);

                const material = this.materials[matIdx][colorIdx];
                const plane = new THREE.Mesh(geometry, material.clone());
                plane.scale.set(1.0, plane.material.map.image.height / plane.material.map.image.width, 1.0);
                plane.seed = Math.random() * 10000;

                plane.position.x = realPos.x;
                plane.position.y = realPos.y;
                plane.rotation.z = Math.random();

                this.scene.add(plane);
                this.paintToRender.push(plane);
            },
            isIOSDevice: function (){
                return !!navigator.platform && /iPad|iPhone|iPod/.test(navigator.platform);
            },
            actualScreenshot: async function () {

                this.removeUI();
                const {width, height} = this;
                console.log("Screenshot", width, height);

                const strMime = "image/png";

                if ( this.isIOSDevice() ) {
                    let data = '';
                    if (!this.screenshotData) {
                        data = await this.renderer.domElement.toDataURL(strMime);
                    } else {
                        data = this.screenshotData;
                    }

                    const image = new Image();
                    image.src = data;
                    // const w = window.open("");
                    // w.document.write(image.outerHTML);
                    document.body.style.overflow = "scroll";
                    document.body.innerHTML = image.outerHTML;

                    alert("Long tap the image to save it to your device");

                    this.takeScreenshot = false;
                    if ( !this.screenshotData ) {
                        this.screenshotData = data;
                    }
                } else {

                    if ( !this.screenshotData ) {
                        this.renderer.domElement.toBlob((blob) => {
                            this.screenshotData = blob;

                            const a = document.createElement('a');
                            a.style.display = "none";
                            const blobUrl = window.URL.createObjectURL(blob);
                            a.href = blobUrl;
                            a.download = `wallpaper.png`;
                            a.click();
                        }, strMime);
                    } else {
                        const a = document.createElement('a');
                        a.style.display = "none";
                        const blobUrl = window.URL.createObjectURL(this.screenshotData);
                        a.href = blobUrl;
                        a.download = `wallpaper.png`;
                        a.click();
                    }
                }



                this.kill();
            },
            screenshot: function () {
                if ( this.screenshotData ) {
                    this.actualScreenshot();

                } else {
                    this.takeScreenshot = true;
                }
            }

        }

    }
</script>

<style scoped>
    .overallContainer {
        width: 100vw !important;
        height: 100vh !important;
        position: relative;
    }

    .canvasContainer {
        position: absolute;
        width: 100vw!important;
        height: 100vh!important;
        display: flex;
        background-color: #000;
        justify-content: center;
        align-items: center;
    }

    .canvasContainer {
        z-index: 200;
    }
</style>