<template>
    <div class="ball-container" ref="centerEl">
        <div class="ball" ref="ballEl"/>
    </div>
</template>

<script>
    import {Expo, Power4, Quad, TweenMax} from 'gsap';

    export default {
        name: "Ball",
        props: ['radius', 'color', 'x', 'y'],
        data() {
            return {
                sx: 0, sy: 0, fx: 0, fy: 0
            }
        },
        mounted() {
            this.fx = Number(this.x);
            this.fy = Number(this.y);
            const xAmount = (screen.width > 2000) ? screen.width : 2000;
            this.sx = Number(this.x) + xAmount
            this.fy = Number(this.y);

            TweenMax.set(this.$refs.centerEl, {x: this.sx, y: this.sy});

            this.$refs.ballEl.style.borderRadius = `${Number(this.radius)}px`;
            this.$refs.ballEl.style.width = `${Number(this.radius) * 2}px`;
            this.$refs.ballEl.style.height = `${Number(this.radius) * 2}px`;
            this.$refs.ballEl.style.marginLeft = `${-Number(this.radius)}px`;
            this.$refs.ballEl.style.marginTop = `${-Number(this.radius)}px`;
            this.$refs.ballEl.style.backgroundColor = this.color;
        },

        methods: {

            animateInFromRight() {

                TweenMax.fromTo(this.$refs.centerEl, (Math.random() * 0.3) + .8, {x: this.sx, y: this.sy}, {
                    force3D: true,
                    overwrite: true,
                    delay: Math.random() * 0.3,
                    x: this.fx,
                    y: this.fy,
                    ease: Expo.easeOut,
                });
            },
            animateInFromLeft() {
                TweenMax.fromTo(this.$refs.centerEl, (Math.random() * 0.3) + .8, {
                    x: this.fx - screen.width,
                    y: this.fy
                }, {
                    force3D: true,
                    overwrite: true,
                    delay: Math.random() * 0.3,
                    x: this.fx,
                    y: this.fy,
                    ease: Power4.easeOut,
                });
            },
            animateOutToRight() {
                TweenMax.to(this.$refs.centerEl, (Math.random() * 0.5) + .8, {
                    force3D: true,
                    overwrite: true,
                    delay: Math.random() * 0.1,
                    x: this.sx,
                    y: this.sy,
                    ease: Quad.easeOut,
                });
            },
            animateOutToLeft() {
                const xAmount = (screen.width > 2000) ? screen.width : 2000;
                TweenMax.to(this.$refs.centerEl, (Math.random() * 0.5) + .8, {
                    force3D: true,
                    overwrite: true,
                    delay: Math.random() * 0.1,
                    x: this.fx - xAmount,
                    y: this.fy,
                    ease: Quad.easeOut,
                });
            },
        }
    }
</script>

<style scoped>
    .ball-container {
        position: absolute;
        z-index: 1000;
        width: 1px;
        height: 1px;
    }
</style>